.nav-main {
  color: #4ea699;
  background-color: #111717;
  padding: 10px 10px;
  height: 55px;
  width: 100vw;
  top: 0px;
  position: -webkit-sticky;
  position: sticky;
  font-size: 16pt;
}
.nav-main ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  padding: 0px;
  margin: 5px 15px;
}
.nav-main a {
  text-decoration: none;
  color: #4ea699;
}